import { getServerApiBase } from "@/lib/api";
import { redirect } from "next/navigation";
import HomeClient from "@/components/home/HomeClient";
import type { HomeResponse } from "@/types/home";
import { fetchServerMe } from "@/lib/server/me";
import { buildServerCookieHeader } from "@/lib/server/cookies";

async function fetchHomeData(): Promise<HomeResponse> {
  const cookieHeader = await buildServerCookieHeader();
  const res = await fetch(`${getServerApiBase()}/home`, {
    headers: cookieHeader ? { Cookie: cookieHeader } : undefined,
    cache: "no-store",
  });
  if (res.status === 401) {
    redirect("/login");
  }
  if (!res.ok) {
    throw new Error("Failed to load dashboard");
  }
  return res.json();
}

export default async function HomePage() {
  const [data, me] = await Promise.all([fetchHomeData(), fetchServerMe()]);
  return <HomeClient initialData={data} me={me} />;
}
